import Head from 'next/head'
import { Menu } from 'antd'

export default () => (
  <div>
    <Head>
      <link rel='stylesheet' href='//cdnjs.cloudflare.com/ajax/libs/antd/2.9.3/antd.min.css' />
    </Head>
    <div className="header">
    <Menu
      mode="horizontal"
      theme="dark"
    >
      <Menu.Item key="product">
        <h3>Product</h3>
      </Menu.Item>
      <Menu.Item key="About">
        <h3>About</h3>
      </Menu.Item>
    </Menu>
    </div>
  </div>
)